import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useI18n } from 'services/i18n';
import useSelectors from 'helpers/useSelectors';
import { useModal } from 'services/modal';
import { ableShareFields } from 'own/contants';
import {
  actions as shareReportActions,
  selectors as shareReportSelectors,
} from 'store/share-report';

import { getPdfAsBase64 } from 'helpers/reports';
import { selectors as userSelectors } from 'store/user';

import {
  DataBox,
  FormInbox,
  FormInboxFieldTypes,
  FormInboxValidationType,
  Button,
} from 'ux-thebackyard';

const defaultShare = {
  [ableShareFields.SHARE_CURRENT_EMAIL]: true,
  [ableShareFields.SHARE_CUSTOM_EMAIL]: [],
};

const ShareReport = ({ element }) => {
  const [currentShare, setCurrentShare] = useState(defaultShare);
  const [submitBtnProps, setSubmitBtnProps] = useState({});

  const dispatch = useDispatch();
  const t = useI18n();

  const { onClose: onModalClose, ModalFooter } = useModal();

  const [
    isBusyShare,
    isSuccessShare,
    isFailedShare,
    userEmail,
  ] = useSelectors(
    shareReportSelectors.getShareIsBusy,
    shareReportSelectors.getShareIsSuccess,
    shareReportSelectors.getShareIsFailed,
    userSelectors.fetchUser.getUserEmail,
  );

  const handleShare = () => {
    const date = new Date();
    const currentDate = `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`;
    const promise = getPdfAsBase64(element, `Reports-${currentDate}`);
    // TODO: Aqui segun el valor del checkbox mandar el email del current user o el email en el input
    promise.then((pdfAsString) => {
      dispatch(shareReportActions.ShareReport({ email: userEmail, pdf: pdfAsString }));
    });
  };

  useEffect(() => {
    if (isSuccessShare) {
      onModalClose();
    }
  }, [isSuccessShare]);

  // TODO: Borrar el texto del email si se marca el checkbox
  const handleOnChange = (value) => {
    setCurrentShare(value);
  };

  /**
   * TODO: A este componente parece que le faltan algunas funcionalidades, <RESOLVER>
   */

  const itemsForm = [
    // {
    //   label: t('my_email'),
    //   fieldType: FormInboxFieldTypes.CHECKBOX,
    //   fieldName: ableShareFields.SHARE_CURRENT_EMAIL, // TODO add Checkbox to library form
    // },
    {
      label: t('email'),
      fieldType: FormInboxFieldTypes.INPUT,
      disabled: currentShare[ableShareFields.SHARE_CURRENT_EMAIL], // TODO no entiendo esto para que es
      fieldName: ableShareFields.SHARE_CUSTOM_EMAIL,
      width: '100%',
      validations: [
        { type: FormInboxValidationType.REQUIRED },
      ],
    },
  ];

  const errorMessage = { type: 'error', text: 'Error compartiendo reporte' }; // TODO translate

  return (
    <>
      <DataBox label={t('share')} separator width="30rem">
        <FormInbox
          items={itemsForm}
          value={currentShare}
          onChange={handleOnChange}
          onSubmit={handleShare}
          changeExternSubmitBtnProps={setSubmitBtnProps}
          display="flex" // TODO check
          alignItems="start" // TODO check
          flexDirection="column" // TODO check
        />
      </DataBox>
      <ModalFooter showSpinner={isBusyShare} message={isFailedShare && errorMessage}>
        <Button
          design="primary"
          rounded
          m="10px"
          p="12px 30px"
          label={t('share')}
          {...submitBtnProps}
        />
      </ModalFooter>
    </>
  );
};

export default ShareReport;
