export const getDefaultDates = () => {
  const today = new Date();
  const past = addMonths(today, -11);
  const newPast = new Date(past.getFullYear(), past.getMonth(), 1);
  return ({
    dateEnd: today.toISOString(),
    dateIn: newPast.toISOString(),
  });
};

