import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { useI18n } from 'services/i18n';
import {
  Avatar, IconButton, ListItemAvatar, ListItemText,
} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import UploadedIcon from '@material-ui/icons/CheckCircleOutline';
import FailedIcon from '@material-ui/icons/HighlightOff';
import NoUploadedIcon from '@material-ui/icons/Publish';

import Unknown from '@material-ui/icons/Help';

import { formatBytes } from 'helpers/general';
import { Box } from 'ux-thebackyard';
import PDFImageSrc from 'media/pdf.png';
import XMLImageSrc from 'media/xml.png';
import { isValidPair } from '../helpers';
// TODO ver si se puede migrar todo estos iconos para la otra library de iconos

const FlexBox = styled(Box)`
  display: flex;
  align-items: center; 
`;

const singleFileStyle = {
  border: 'solid 1px red',
  backgroundColor: 'rgba(240,0,0,0.1)',
  borderRadius: '5px',
};

const pairFileStyle = {
  border: 'solid 1px transparent',
  borderRadius: '5px',
};

const StyledImg = styled('img')`
  width: 1.8rem;
`;

export const states = {
  FAILED: 'failed',
  UPLOADED: 'uploaded',
  NO_UPLOADED: 'noUploaded',
};

const FileListItem = ({ filesPair, onDelete }) => {
  const t = useI18n();
  const { pdf, xml, state = states.NO_UPLOADED } = filesPair || {};
  const isValid = isValidPair(filesPair);

  return (
    <FlexBox pl="2rem" pr="2rem" mt="10px" {...(isValid ? pairFileStyle : singleFileStyle)}>
      <FlexBox width="45%">
        <ListItemAvatar>
          <Avatar>
            {pdf ? (
              <StyledImg src={PDFImageSrc} alt="pdf" />
            ) : (
              <Unknown />
            )}

          </Avatar>
        </ListItemAvatar>
        <ListItemText
          primary={pdf?.name || t('unknownFile')}
          secondary={(pdf?.size && formatBytes(pdf?.size)) || null}
        />
      </FlexBox>

      <FlexBox width="45%">
        <ListItemAvatar>
          <Avatar>
            {xml ? (
              <StyledImg src={XMLImageSrc} alt="xml" />
            ) : (
              <Unknown />
            )}
          </Avatar>
        </ListItemAvatar>
        <ListItemText
          primary={xml?.name || t('unknownFile')}
          secondary={(xml?.size && formatBytes(xml?.size)) || null}
        />
      </FlexBox>

      <Box ml="auto" display="flex" alignItems="center">
        {isValid && (
        <>
          {state === states.NO_UPLOADED && <NoUploadedIcon color="primary" />}
          {state === states.UPLOADED && <UploadedIcon style={{ color: 'green' }} />}
          {state === states.FAILED && <FailedIcon color="error" />}
        </>
        )}

        <IconButton edge="end" aria-label="state">
          <DeleteIcon onClick={onDelete} />
        </IconButton>
      </Box>
    </FlexBox>
  );
};

const filePropType = PropTypes.shape({
  name: PropTypes.string,
  size: PropTypes.number,
});

FileListItem.propTypes = {
  filesPair: PropTypes.shape({
    pdf: filePropType,
    xml: filePropType,
    state: PropTypes.oneOf(Object.values(states)),
  }).isRequired,
  onDelete: PropTypes.func,
};

export default FileListItem;
