import { Auth, Amplify, API as API_AWS } from 'aws-amplify';
import { paramsSerializer } from './tranmuter';

import authConfig, {
  INGESTA, MANAGEMENT, DB_SERVICES,
} from './authConfig';

Amplify.configure(authConfig);

const API = async({
  method,
  endpoint,
  path,
  params,
  data: body,
}) => {
  let newParams = { ...params };
  if (endpoint === DB_SERVICES) { // SHIT
    const { email } = await (await Auth.currentSession()).getIdToken().payload;
    newParams = { ...newParams, email };
  }

  const headers = {
    userAuthorization: `Bearer ${(await Auth.currentSession()).getIdToken().getJwtToken()}`,
  };

  const pathWithQueries = newParams ? `${path}?${paramsSerializer(newParams)}` : path;
  return API_AWS[method](endpoint, pathWithQueries, { body, headers });
};

