import { isEmpty, isArray } from 'lodash';
import qs from 'query-string';

const fromEntries = (arr) => arr.reduce((p, [key, v]) => ({ ...p, [key]: v }), {});
const removeFalsyValues = (params) => fromEntries(
  Object.entries(params)
    .filter(
    //   ([key, value]) => value && !isEmpty(value),
      ([, value]) => value || (isArray(value) && !isEmpty(value)),
    ),
);

export const toAPI = (params) => removeFalsyValues(params);

export const paramsSerializer = (params) => qs.stringify(params, { arrayFormat: 'comma' });
