export function* getDefaultRFCParams() {
  const { key, value } = yield select(userSelectors.fetchRFCs.getMyRFCsFields);
  return { [key]: value };
}

export function* checkDefaultParams(params) {
  const rfcParams = yield call(getDefaultRFCParams);

  if (params[ableParamsToSearchDocs.UUID]) {
    return {
      [ableParamsToSearchDocs.UUID]: params[ableParamsToSearchDocs.UUID],
    };
  }

  return {
    ...rfcParams,
    [ableParamsToSearchDocs.PAGE]: 1,
    [ableParamsToSearchDocs.PAGE_SIZE]: pageSize,
    ...getDefaultDates(),
    ...params,
  };
}

export function* checkDefaultReportsParams(params) {
  const rfcParams = yield call(getDefaultRFCParams);

  return {
    ...rfcParams,
    ...getDefaultDates(),
    ...params,
  };
}
