import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useI18n } from 'services/i18n';

import useSelectors from 'helpers/useSelectors';
import useSearch from 'services/search/useSearch';
import DocsUploader from 'components/docs-uploader';
import { paramsToFetchPC, cfdiStatesValues, ableParamsToFetch } from 'own/contants';
import { typeDocNameItemsDropdownSelect } from 'own/helpers';
import {
  SvgSyncSolid,
} from 'react-line-awesome-svg';

import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/documents';

import {
  DataBox,
  Accordion,
  PageInbox,
  TableBox,
  FormInbox,
  FormInboxFieldTypes,
} from 'ux-thebackyard';
import PaymentComplementTrans from 'own/table-transmuters/payment-complement';

import { selectors as userSelectors, actions as userActions } from 'store/user';

const PaymentComplements = () => {
  const t = useI18n();
  const dispatch = useDispatch();
  const { search, params, updateParams } = useSearch();

  const [
    rfcsIssuer,
    rfcsReceiver,

    pcs,
    isBusy,
    pagination,
  ] = useSelectors(
    userSelectors.fetchRFCs.getRFCsIssuer,
    userSelectors.fetchRFCs.getRFCsReceiver,

    docsSelectors.getDocs1,
    docsSelectors.getDocs1IsBusy,
    docsSelectors.getDocs1Pagination,
  );

  const FetchPCs = () => dispatch(docsActions.FetchDocs1({ ...params, ...paramsToFetchPC }));

  const handleChangePage = (pageT) => updateParams({ page: pageT });

  useEffect(() => {
    if (!(rfcsReceiver || rfcsIssuer)) dispatch(userActions.fetchRFCs.Call());
  }, [rfcsIssuer, rfcsReceiver]);

  useEffect(() => {
    FetchPCs();
  }, [search]);

  const formConfig = [
    {
      label: t('receiver_rfc'),
      enableSearch: true,
      items: rfcsReceiver,
      fieldType: FormInboxFieldTypes.DROPDOWN_SELECT,
      fieldName: ableParamsToFetch.RFC_RECEIVER,
    },
    {
      label: t('issuing_rfc'),
      enableSearch: true,
      items: rfcsIssuer,
      fieldType: FormInboxFieldTypes.DROPDOWN_SELECT,
      fieldName: ableParamsToFetch.RFC_ISSUER,
    },
    {
      label: t('state'),
      enableSearch: true,
      items: cfdiStatesValues,
      fieldType: FormInboxFieldTypes.DROPDOWN_SELECT,
      fieldName: ableParamsToFetch.STATE,
    },
    {
      label: t('date'),
      fieldType: FormInboxFieldTypes.DATE_RANGE,
      fieldNameStartDate: ableParamsToFetch.DATE_IN,
      fieldNameEndDate: ableParamsToFetch.DATE_END,
    },
    {
      label: t('uuid_fiscal_folio'),
      fieldType: FormInboxFieldTypes.INPUT,
      fieldName: ableParamsToFetch.UUID,
    },
    {
      label: t('total'),
      fieldType: FormInboxFieldTypes.NUMERIC_RANGE,
      fieldNameStartValue: ableParamsToFetch.TOTAL_IN,
      fieldNameEndValue: ableParamsToFetch.TOTAL_END,
    },
    {
      label: t('serie'),
      fieldType: FormInboxFieldTypes.INPUT,
      fieldName: ableParamsToFetch.SERIE,
    },
    {
      label: t('folio'),
      fieldType: FormInboxFieldTypes.INPUT,
      fieldName: ableParamsToFetch.FOLIO,
      type: 'number',
    },
    {
      label: t('type'),
      fieldType: FormInboxFieldTypes.DROPDOWN_SELECT,
      items: typeDocNameItemsDropdownSelect,
      optionTextField: 'name',
      optionValueField: 'type',
      fieldName: ableParamsToFetch.TYPE_DOC,
    },
  ];

  const headBtnsProps = [
    {
      label: t('update'), design: 'shadow', svg: SvgSyncSolid, rounded: true, onClick: FetchPCs,
    },
  ];

  return (
    <PageInbox label={t('payment_complements')}>

      <DataBox label={t('upload_payment_complements')} separator>
        <DocsUploader />
      </DataBox>

      <DataBox
        label={t('payment_complement_list')}
        headBtnsProps={headBtnsProps}
        separator
      >
        <Accordion label={t('filter_search')}>
          <FormInbox
            items={formConfig}
            value={params}
            onSubmit={updateParams}
            submitBtnProps={{ label: t('apply') }}
          />
        </Accordion>

        <TableBox
          transmuter={PaymentComplementTrans.transmuter}
          heads={PaymentComplementTrans.heads}
          data={pcs}
          isBusy={isBusy}
          noFoundMessage={t('no_payment_complements_found')}
          pagination={{
            page: pagination?.page,
            totalPages: pagination?.totalPages,
            totalItems: pagination?.totalDocs,
            pageSize: 50, // TODO
          }}
          onChangePage={handleChangePage}
          stickyHeaders
        />
      </DataBox>

    </PageInbox>
  );
};

export default PaymentComplements;
